* {
  margin: 0px;
}

body{
  background: #EFEFBB;
  background: -webkit-linear-gradient(to right, #D4D3DD, #EFEFBB);
  background: linear-gradient(to right, #D4D3DD, #EFEFBB);
}

nav {
  height: 100px;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}

.navbar-img {
  width: 160px;
}

.searchbar-container {
  display: flex;
  margin: 0px auto;
  padding: 0 20px;
  align-items: center;
}

.searchbar {
  background-color: white;
  margin-right: 20px;
}

.searchbar input {
  padding: 10px;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
  border-radius: 3px;
  border: none;
}

.searchbar-btn button {
  background-color: #0e6f9f;
  border: none;
  border-radius: 10px;
  height: 44px;
  color: white;
  padding: 10px 12px;
}

.pokedex-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
}

.pokedex-grid {
  display: grid;
  gap: 10px;
  grid-template-columns: repeat(3, 1fr);
}

.pokemon-card {
  display: flex;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 3px;
}

.pokemon-image{
  width: 70px;
  height: 70px;
  margin-right: 10px;
}

.pokemon-image-container{
  padding: 0px 5px;
}

.card-body{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px 10px 10px 0px;
  flex: 1;
}
.card-top{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.card-top h3{
  text-transform: capitalize;
}
.card-bottom{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.pokemon-type{
  display: flex;
}
.pokemon-type-text{
  border: 1px solid white;
  border-radius: 30px;
  padding: 5px 15px;
  margin-right: 10px;
  text-transform: capitalize;
}

.pagination-container{
  display: flex;
  flex-direction: row;
  align-items: center;
}

.pokemon-heart-btn{
  border: none;
  background-color: white;
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.25);
  padding: 4px;
  cursor: pointer;
  align-items: center;
}

.not-found-text{
  text-align: center;
  font-size: 1.25em;
  padding: 25px;
}

.App {
  text-align: center;
}